:root {
    --main-color: #663399;
    --main-color-light: #ad73e7;
    --main-font: 'Kanit', sans-serif;
}
.homeContainer {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 100vh;
    margin-left: auto;
    margin-right: auto;
}
.homeNavBarContainer {
    width: 100vw;
    height: 10%;
}
.ttcLogoImage {
    margin: 2rem 0 0 3rem;
    max-width: 10rem;
}
.homeBody {
    margin: 0 auto 0 auto;
    padding: 2rem 0 0 0;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
.homeDescription {
    margin: 2rem 0 0 0;
    font-size: 30px;
    font-family: var(--main-font);
    color: var(--main-color);
    font-weight: 700;
}
.homeDescription:after {
    content: ".";
    color: black;
}
.homeUrlTextField {
    margin: 2rem 0 0 0;
    font-size: 16px;
    font-family: var(--main-font);
    padding: 0.5rem 0.5rem;
    background-color: #fff;
    border: 2px solid grey;
    border-radius: 4px;
    transition: 0.5s all;
}
.homeUrlTextField:focus {
    border-color: var(--main-color);
}
.homeButtonsContainer {
    /*border: 2px solid red;*/
    display: flex;
    width: 100%;
    margin: 2rem 0 0 0;
    flex-direction: row;
    justify-content: space-around;
}
.homeFooter {
    margin-top: auto;
    height: 15%;
    background-color: var(--main-color);
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
}
.footerContent {
    margin-right: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
}
.developedByText {
    color: #ddd;
    font-family: 'Source Code Pro', monospace;
}
.developerName {
    color: #fff;
    font-family: 'Source Code Pro', monospace;
}
